import { Component, OnInit} from '@angular/core';
import { ConfigService } from '../../../controllers/config.service';
import { NgForm } from '@angular/forms';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


declare var $:any;

@IonicPage()
@Component({
  selector: 'config',
  templateUrl: 'config.html'
})
export class ConfigComponent implements OnInit {

  modalidades = [];
  ciudades = [];
  trabajos = [];

  constructor(private configuracion: ConfigService) {
    this.getCiudades();
    this.getModalidades();
    this.getTrabajos();
  }

  ngOnInit() {
  }


  getModalidades() {
    this.configuracion.getModalidad()
      .subscribe(res => {
        console.log(res);
        this.modalidades = res;
      });
  }

  /* -------------------- GET DE CONFIGURACION ------------------ */
  getCiudades() {
    this.configuracion.getCiudad()
      .subscribe(data => {
        console.log(data);
        this.ciudades = data;
      });
  }

  getTrabajos() {
    this.configuracion.getTrabajo()
      .subscribe(res => {
        console.log(res);
        this.trabajos = res;
      });
  }

  postModalidades(fr: NgForm) {
    const value = fr.value.nombre_modalidad;
    console.log(value);
    const datos = {
      'std_modalidad': true,
      'nombre_modalidad': value,
      'tipo_modalidad': 'MIHOGAR'
    };

    if (value !== undefined && value !== null && value !== '') {
      this.configuracion.postModalidades(datos)
        .subscribe(res => {
          console.log(res);
          this.getModalidades();
        });
      fr.reset();
    }
  }

  postCiudades(fr: NgForm) {
    const value = fr.value.nombre_ciudad;
    console.log(value);
    const datos = {
      'std_ciudad': true,
      'nombre_ciudad': value,
      'tipo_ciudad': 'MIHOGAR'
    };

    if (value !== undefined && value !== null && value !== '') {
      this.configuracion.postCiudades(datos)
        .subscribe(res => {
          console.log(res);
          this.getCiudades();
        });
      fr.reset();
    }
  }


  postTrabajos(fr: NgForm) {
    const value = fr.value.nombre_trabajo;
    console.log(value);
    const datos = {
      'std_trabajo': true,
      'nombre_trabajo': value,
      'tipo_trabajo': 'MIHOGAR'
    };

    if (value !== undefined && value !== null && value !== '') {
      this.configuracion.postTrabajos(datos)
        .subscribe(res => {
          console.log(res);
          this.getTrabajos();
        });
      fr.reset();
    }
  }

  deleteTrabajo(id) {
    console.log(id);
    const value = id;
    if (value !== undefined && value !== null && value !== '') {
      this.configuracion.deleteTrabajos(value)
        .subscribe(res => {
          console.log(res);
          this.getTrabajos();
        });
    }
  }


  deleteCiudad(id) {
    console.log(id);
    const value = id;
    if (value !== undefined && value !== null && value !== '') {
      this.configuracion.deleteCiudades(value)
        .subscribe(res => {
          console.log(res);
          this.getCiudades();
        });
    }
  }

  deleteModalidad(id) {
    console.log(id);
    const value = id;
    if (value !== undefined && value !== null && value !== '') {
      this.configuracion.deleteModalidades(value)
        .subscribe(res => {
          console.log(res);
          this.getModalidades();
        });
    }
  }

  editModalidad(dts_mod) {
    // console.log(dts_mod);
    $('#edit_nm_mod').val(dts_mod.nombre_modalidad);
    if (dts_mod.std_modalidad == '0') {
      $("#edit_std_mod").prop('checked', false);
      $("#std_mod_txt").text(' INACTIVO');
    }

    else {
      $("#edit_std_mod").prop('checked', true);
      $("#std_mod_txt").text(' ACTIVO');
    }

    $("#edit_nm_mod").val(dts_mod.nombre_modalidad);
    $("#edit_std_mod").val(dts_mod.std_modalidad);
    $("#edit_origen_mod").val(dts_mod.tipo_modalidad);


  }
  

}



